export function getDB(cb) {
  var res;
  axios.get('https://docs.google.com/spreadsheets/d/1npVr6xkN7hBzMQe-x_ao0T_tTDVTTzLetgvCUtG4k70/export?format=csv&id=1qNBuXr5KIHPHqoNBgZEao2F3rAjBtMiQf6fsEDug0mk&gid=0').then(res => {
    var data = {};
    var raw = res.data.split('\r\n');
    var tmp;
    for (var idx = 1; idx < raw.length; idx++) {
      tmp = raw[idx].split(',')
      data[tmp[0]] = tmp[1]
    }
    cb(data)
  })
}